import { stopSubmit } from "redux-form";
import { authAPI } from "../api/api";

const SET_USER_DATA = 'auth/SET_USER_DATA',
    SHOW_ERROR = 'auth/SHOW_ERROR',
    SET_CAPTCHA = 'auth/SET_CAPTCHA'

let initialState = {
    email: null,
    id: null,
    login: null,
    isAuth: false,
    generalError: null,
    captchaUrl: null
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.userData,
                isAuth: action.isAuth,
                generalError: null
            }
        case SHOW_ERROR:
            return {
                ...state,
                generalError: action.error
            }
        case SET_CAPTCHA:
            return {
                ...state,
                captchaUrl: action.url
            }
        default:
            return state;
    }

}

const handleError = (response, dispatch) => {
    let error = response.messages[0];
    dispatch(setGeneralError(error))
}

export default authReducer;

//AC

export const setUserData = (userData, isAuth) => ({
    type: SET_USER_DATA,
    userData,
    isAuth
})

export const setGeneralError = (error) => ({
    type: SHOW_ERROR,
    error
})

const setCaptcha = (url) => ({
    type: SET_CAPTCHA,
    url
})

//TC

export const getUserData = () => async (dispatch) => {
    let response = await authAPI.getUserData();
    if (response.resultCode === 0) {
        dispatch(setUserData({ ...response.data }, true))
    }
    return response;
}

export const logIn = (authData) => async (dispatch) => {
    let response = await authAPI.logIn(authData)

    //resultCode === 10, если нужно ввести капчу
    response.resultCode === 10 && dispatch(getCaptcha())

    if (response.resultCode === 0) {
        dispatch(getUserData())
        dispatch(setCaptcha(null))
    }
    else handleError(response, dispatch);
}

export const logOut = () => async (dispatch) => {
    let response = await authAPI.logOut()
    response.resultCode === 0 ? dispatch(setUserData({
        email: null,
        id: null,
        login: null,
    }, false)) : console.log(response);

}

export const getCaptcha = () => async (dispatch) => {
    let response = await authAPI.getCaptcha()
    response.status === 200 ? dispatch(setCaptcha(response.data.url)) : console.log(response);
}

