import { profileAPI, usersAPI } from "../api/api";
import { selectIsFollowed } from "./profilePageSelectors";

const ADD_POST = "profile/ADD-POST",
    SET_PROFILE_INFO = "profile/GET_PROFILE_INFO",
    SET_PROFILE_PHOTOS = "profile/SET_PROFILE_PHOTOS",
    SET_STATUS = "profile/SET_STATUS",
    INITIALIZE_SUCCESS = "profile/INITIALIZE_SUCCESS",
    SET_FRIENDS = "profile/SET_FRIENDS",
    SET_IS_FOLLOWED = "profile/SET_IS_FOLLOWED",
    SET_FOLLOW_IS_FETCHING = "profile/SET_FOLLOW_IS_FETCHING";

let initialState = {
    isInitialized: false,
    isFollowed: null as null | boolean,
    followIsFetching: false,
    profileInfo: null as null | profileInfoType,
    profileStatus: null as null | string,
    friends: [] as Array<friendInfoType>,
    friendsCount: null as null | number,
    posts: {
        22988: [
            {
                authorId: 22988,
                author: "datfeela",
                date: "15.01.2022",
                id: 2,
                text: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Deserunt, incidunt ex laboriosam totam ipsa, nobis et sint dicta atque a harum amet maxime esse expedita saepe doloribus quo tempore quos quidem exercitationem modi architecto accusantium? Porro facere pariatur architecto suscipit ducimus perferendis, in voluptas veniam perspiciatis dolore accusamus sint quibusdam!",
            },
            {
                authorId: 23506,
                author: "datfeelaTest",
                id: 1,
                date: "01.05.2021",
                text: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Nesciunt, rem odit. Animi magni provident rerum veritatis nam iste illum dolor, neque blanditiis consectetur. Ducimus dicta facilis maxime esse incidunt, ut, excepturi ipsum sunt reiciendis similique ad sapiente exercitationem optio unde nulla sit possimus inventore rem aliquid accusantium cupiditate quos laborum pariatur! Doloribus esse facere perspiciatis veniam neque pariatur vitae iste eius fugiat nihil eos natus provident rerum, ducimus cum rem soluta quisquam aspernatur animi saepe non nisi culpa? Laudantium eius doloribus numquam repudiandae at aspernatur id distinctio vero voluptate reprehenderit deleniti, aliquid accusantium fugit dolorem! Maxime excepturi adipisci in. Odit.",
            },
        ],
    } as postsType,
};
export const stateType = typeof initialState;

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case INITIALIZE_SUCCESS:
            return {
                ...state,
                isInitialized: true,
            };
        case ADD_POST:
            let newPost: postType = {
                authorId: action.author.id,
                author: action.author.name,
                date: action.date,
                text: action.text,
                id: state.posts[action.targetUserId] ? state.posts[action.targetUserId].length + 1 : 1,
            };
            //Добавляю для целевого пользователя пустой массив, в который будут добавляться посты, если у пользователя еще нет постов
            if (!state.posts[action.targetUserId]) {
                state.posts = { ...state.posts, [action.targetUserId]: [] };
            }
            return {
                ...state,
                posts: { ...state.posts, [action.targetUserId]: [newPost, ...state.posts[action.targetUserId]] },
            };
        case SET_PROFILE_INFO:
            return {
                ...state,
                profileInfo: action.profileInfo,
            };
        case SET_PROFILE_PHOTOS:
            return {
                ...state,
                profileInfo: { ...state.profileInfo, photos: action.photos },
            };
        case SET_STATUS:
            return {
                ...state,
                profileStatus: action.status,
            };
        case SET_FRIENDS:
            return {
                ...state,
                friends: action.friends,
                friendsCount: action.friendsCount,
            };
        case SET_IS_FOLLOWED:
            return {
                ...state,
                isFollowed: action.isFollowed,
            };
        case SET_FOLLOW_IS_FETCHING:
            return {
                ...state,
                followIsFetching: action.isFetching,
            };
        default:
            return state;
    }
};

export default profileReducer;

//AC

export type setInitializeSuccessType = () => {
    type: typeof INITIALIZE_SUCCESS;
};
export type setProfileInfoType = (profileInfo: profileInfoType) => {
    type: typeof SET_PROFILE_INFO;
    profileInfo: profileInfoType;
};
export type savePhotoSuccessType = (photos: photosType) => { type: typeof SET_PROFILE_PHOTOS; photos: photosType };
export type setStatusType = (status: string) => {
    type: typeof SET_STATUS;
    status: string;
};
export type addPostSuccessType = (
    text: string,
    author: { id: number; name: string },
    date: string,
    targetUserId: number
) => {
    text: string;
    author: { id: number; name: string };
    date: string;
    targetUserId: number;
};

const setInitializeSuccess: setInitializeSuccessType = () => ({
    type: INITIALIZE_SUCCESS,
});

export const setProfileInfo: setProfileInfoType = (profileInfo: profileInfoType) => ({
    type: SET_PROFILE_INFO,
    profileInfo,
});

export const savePhotoSuccess: savePhotoSuccessType = (photos: photosType) => ({
    type: SET_PROFILE_PHOTOS,
    photos,
});

export const setStatus: setStatusType = (status) => ({
    type: SET_STATUS,
    status,
});

export const addPostSuccess: addPostSuccessType = (text, author, date, targetUserId) => ({
    type: ADD_POST,
    text,
    author,
    date,
    targetUserId,
});

const getFriendsSuccess = (friends, friendsCount) => ({
    type: SET_FRIENDS,
    friends,
    friendsCount,
});

const setIsFollowed = (isFollowed) => ({
    type: SET_IS_FOLLOWED,
    isFollowed,
});

const setFollowIsFetching = (isFetching) => ({
    type: SET_FOLLOW_IS_FETCHING,
    isFetching,
});

//TC

export const addPost = (text: any, targetUserId: any) => async (dispatch: any, getState: any) => {
    //костыль для создания автора и даты поста без серверного api
    const date = `${new Date().toLocaleDateString()}`;
    const authorId = getState().auth.id;

    let response = await profileAPI.getProfileInfo(authorId);
    let author = {
        id: response.userId,
        name: response.fullName,
    };
    dispatch(addPostSuccess(text, author, date, targetUserId));
};

export const initializeProfile = (userId: any, isOwner: any) => async (dispatch: any) => {
    const profileInfoPromise = dispatch(getProfileInfo(userId));
    const statusPromise = dispatch(getStatus(userId));
    const getFriendsPromise = isOwner && dispatch(getFriends());
    const isFollowedPromise = !isOwner && dispatch(getIsFollowed(userId));
    Promise.all([profileInfoPromise, statusPromise, getFriendsPromise, isFollowedPromise]).then(() => {
        dispatch(setInitializeSuccess());
    });
};

export const getProfileInfo = (userId: any) => async (dispatch: any) => {
    const response = await profileAPI.getProfileInfo(userId);
    dispatch(setProfileInfo(response));
    return response;
};

export const getFriends = () => async (dispatch: any) => {
    let response = await usersAPI.getUsers(6, 1, true, "");
    dispatch(getFriendsSuccess(response.items, response.totalCount));
    return response;
};

export const getIsFollowed = (userId: any) => async (dispatch: any) => {
    let response = await usersAPI.getIsFollowed(userId);
    dispatch(setIsFollowed(response));
    return response;
};

export const changeProfileInfo =
    (userId: any, fullName: any, aboutMe: any, lookingForAJob: any, lookingForAJobDescription: any, contacts: any) => async (dispatch: any) => {
        let response = await profileAPI.setProfileInfo(userId, fullName, aboutMe, lookingForAJob, lookingForAJobDescription, contacts);
        debugger;
        response.resultCode === 0 ? dispatch(getProfileInfo(userId)) : console.log(response);
        return response;
    };

export const getStatus = (userId: any) => async (dispatch: any) => {
    let response = await profileAPI.getStatus(userId);
    dispatch(setStatus(response));
    return response;
};

export const updateStatus = (status: any) => async (dispatch: any) => {
    let response = await profileAPI.updateStatus(status);
    response.resultCode === 0 ? dispatch(setStatus(status)) : console.log(response);
};

export const savePhoto = (photo: any) => async (dispatch: any) => {
    let response = await profileAPI.setPhoto(photo);
    response.resultCode === 0 ? dispatch(savePhotoSuccess(response.data.photos)) : console.log(response);
    return response;
};

export const subscribe = (userId: any) => async (dispatch: any, getState: any) => {
    const isFollowed = selectIsFollowed(getState());
    dispatch(setFollowIsFetching(true));

    let followResponse = isFollowed ? await usersAPI.unfollow(userId) : await usersAPI.follow(userId);
    followResponse.resultCode === 0 ? dispatch(setIsFollowed(!isFollowed)) : console.log(followResponse);
    dispatch(setFollowIsFetching(false));
};

//types
export type profileInfoType = {
    aboutMe: string;
    contacts: {
        facebook: string;
        github: string;
        instagram: string;
        mainLink: string;
        twitter: string;
        vk: string;
        website: string;
        youtube: string;
    };
    fullName: string;
    lookingForAJob: boolean;
    lookingForAJobDescription: "string";
    photos: photosType;
    userId: number;
};
export type friendInfoType = {
    followed: boolean;
    id: number;
    name: string;
    photos: photosType;
    status: string;
    uniqueUrlName: null;
};
export type photosType = {
    large: string;
    small: string;
};
export type postType = {
    authorId: number;
    author: string;
    date: string;
    id?: number;
    text: string;
};
export type userPostsType = Array<postType>;
export interface postsType {
    [key: number]: Array<postType>;
}
